

body, html {
  width: 100%;
  height: 100%;
  margin: 0px;
  padding: 0px;
}



body {
	font-family: 'Open Sans', sans-serif;
	font-size:14px;
	font-weight:normal;
	margin:0;
}

h1{
	font-size:34px;
	font-family: 'Montserrat', sans-serif;
	font-weight:700;
	letter-spacing: -1px;
	margin:0 0 15px 0;
	text-align:center;
	text-transform:uppercase;
}

h3{
	font-family: 'Montserrat', sans-serif;
	font-size:18px;
	margin:0 0 5px 0;
	text-transform:uppercase;
	font-weight:400;
}

h4{
	font-family: 'Montserrat', sans-serif;
	font-size:16px;
	margin:0 0 5px 0;
	text-transform:uppercase;
	font-weight:400;
}

h6{
	font-size:16px;
	color:#888888;
	font-family: 'Open Sans', sans-serif;
	font-weight:400;
	text-align:center;
	margin:0 0 60px 0;
}

p{
	line-height:24px;
  color:#888888;
	margin:0;
}


a{
    color: white;
}

a:hover {
    color: blue;
}

.btn-xl {
  text-transform: uppercase;
  padding: 1rem 2.2rem;
  font-size: 0.7rem;
  font-weight: 700;
  letter-spacing: 0.1rem;
}

.btn-primary {
  background-color: transparent;
  border-color: white;
  border-width: 0.1rem;
}

.btn-primary:active, .btn-primary:focus, .btn-primary:hover {
  background-color: #00bcd4 !important;
  border-color: #00bcd4 !important;
}

.rounded-pill {
  border-radius: 5rem;
}


.text-white {
  color: white !important;
}

.text-black {
  color: black;
}

.bg-black {
  background-color: black;
  color: white;
}

.bg-gray {
  background-color: #C8C9C7;
}

.bg-blue {
  background-color: #00bcd4;
}

.bg-blue h1,
.bg-blue h2,
.bg-blue h3,
.bg-blue h4,
.bg-blue h5,
.bg-blue h6,
.bg-blue p {
  color: white;
}

.center-page-caption{
    position: absolute;
    top: 0;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
    align-items: center;
    width: 100%;
    height: 100%;
}

.overlay{
    display: block;
    width: 100%;
    background-color: black;
    opacity: 0.3;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}

.full-width {
    width: 100%;
}

.main-heading{
  font-size: 60px;
}

@media(max-width: 768px) {
    .main-heading {
        font-size: 50px;
    }

    .small-heading {
        font-size: 20px;
    }
}

.nav img {
  max-height: 20px !important;
  -webkit-filter: brightness(250%);
          filter: brightness(250%);
}

.nav {
  text-transform: uppercase;
  position: fixed;
  height: auto;
  line-height: 53px;
}

p {
  font-size: 20px;
}

#problem h3 {
  font-size: 25px;
}


#problem .full-div-image {
  -webkit-filter: grayscale(100%);
  filter: gray;
  height: 250px;
}

#problem .full-div-image:hover {
  -webkit-filter: grayscale(0%);
  filter: gray;
  height: 250px;
}

.service-list{
	padding:0 0 0 0;
	font-size:15px;
	margin-bottom:40px;
}
.service-list-col1{
	float:left;
	width:60px;
  margin-right: 30px;
}

.service-list-col1 .icon{
	font-style:normal;
	font-size:50px;
	display:block;
	color:#222;
	font-family: 'FontAwesome';
	line-height:38px;
}
.service-list-col2{
	overflow:hidden;
}

img {
  max-height: 400px;
}

p {
	font-size: 14px;
}


.cards .card {
	box-shadow: 0 2px 5px 0 rgba(0, 0, 0, .16), 0 2px 10px 0 rgba(0, 0, 0, .12);
	font-weight: 400;
	position: relative;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	min-width: 0;
	word-wrap: break-word;
	background-color: #fff;
	background-clip: border-box;
	border: 1px solid rgba(0, 0, 0, .125);
    border-radius: .9rem;
    margin-top: 30px;
}


.cards img.img-fluid {
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, .16), 0 2px 10px 0 rgba(0, 0, 0, .12);
    height: auto;
    width: 400px;
    object-fit: scale-down;
    padding: 5px;
}

.cards .rgba-white-slight, .rgba-white-slight:after {
    background-color: rgba(255,255,255,.1);
}

.cards a {
    font-size: 30px;
    margin: 0 5px 0 5px;
    color: #00bcd4;
}

.subtitle {
    font-size: 85%;
}

.text {
    font-size: 80%;
}

.cardTitle {
    font-weight: 700;
    font-size: 105%;
}

.alex {
    width:34%;
    margin: auto;
}

.photo {
    border-radius: .9rem .9rem 0 0;
}

@media only screen and (min-width: 824px) {
    .deck {
        margin: 0 10% 0 10%;
    }
    
    .deck2 {
        margin: 0 15% 0 15%;
    }
}
#features .full-div-image {
  height: 455px;
}

.icon-button {
  font-size: 90px;
  margin-bottom: 20px;
  margin-top: 20px;
}

.icon-button:hover {
  /*background-color: #bd0760 !important;
  border-color: #bd0760 !important;*/
  color: #00bcd4 !important;
}

.scooter-logo {
  max-height: 110px;
  -webkit-filter: brightness(0) invert(1);
          filter: brightness(0) invert(1);
  padding-left: 30px;
  padding-right: 30px;
}

.compatibility-logos {
  margin-bottom: 50px;
}

.footer img {
    height: 20px;
    -webkit-filter: brightness(0) invert(1);
            filter: brightness(0) invert(1);
    margin-left: 45%;
}
